@media screen and (min-width: 320px) {
  .grid-container {
    display: grid;
    width: 100vw;
    height: 100vh;
    grid-template-columns: 100%;
    grid-template-rows: 100px auto auto;
    grid-template-areas:
    "search-header"
    "search-list"
    "map";
    /*grid-gap: 0.2em;*/
    
  }

  .search-location-header-component {
    grid-area: search-header;
  }

  .list-location-component {
    grid-area: search-list;
    height: 130%;
  }

  .map-container-component {
    grid-area: map;
  }

  .search-locations-bar input {
    width: 60%;
  }

  .search-locations-title {
    margin: 10px;
  }

  .search-locations-title h1 {
    margin: auto;
    text-align: left;
  }

  .infowindow-data {
    width: 200px;
  }

  .infowindow-data h1 {
    text-align: center;
    font-size: 1.2em;
  }

  .infowindow-data img {
    float: left;
    width: 40%;
    height: 75%;
    padding: 0 10px 0 0;
  }

  .infowindow-data p {
    font-family: arial;
    font-size: 0.85em;
  }

  .infowindow-link {
    text-align: right;
    margin-right: 1em;
  }

  .infowindow-link p {
    font-style: italic;
    margin-top: 1em;
    font-size: 0.85em;
  }
}


@media screen and (min-width: 550px) {
  .grid-container {
    display: grid;
    width: 100vw;
    height: 100vh;
    grid-template-columns: 30% 70%;
    grid-template-rows: 15% 85%;
    grid-template-areas:
    "search-header map-header"
    "search-list map";
    /*grid-gap: 0.2em;*/
    
  }

  .search-location-header-component {
    grid-area: search-header;
  }

  .map-header-component {
    grid-area: map-header;
    width: 150%;
  }

  .list-location-component {
    grid-area: search-list;
    height: 120%;
  }

  .map-container-component {
    grid-area: map;
  }

  .search-locations-bar input {
    width: 80%;
  }

  .search-locations-title {
    vertical-align: middle;
  }

  .search-locations-title h1 {
    margin: auto ;
    text-align: left;
  }

  .infowindow-data {
    width: 300px;
  }

  .infowindow-data h1 {
    text-align: center;
    font-size: 1.1em;
  }

  .infowindow-data img {
    float: left;
    width: 25%;
    height: 50%;
    padding: 0 10px 0 0;
  }

  .infowindow-data p {
    font-family: arial;
    font-size: 0.95em;
  }

  .infowindow-link p {
    font-style: italic;
    margin-top: 1em;
    font-size: 0.95em;
  }
}

@media screen and (min-width: 900px) {
  .grid-container {
    display: grid;
    width: 100vw;
    height: 100vh;
    grid-template-columns: 20% 80%;
    grid-template-rows: 10% 90%;
    grid-template-areas:
    "search-header map-header"
    "search-list map";
    /*grid-gap: 0.2em;*/ 
  }

  .search-location-header-component {
    grid-area: search-header;
  }

  .map-header-component {
    grid-area: map-header;
    width: 125%;
  }

  .list-location-component {
    grid-area: search-list;
    height:110%;
  }

  .map-container-component {
    grid-area: map;
  }

  .search-locations-bar input {
    width: 80%;
  }

  .search-locations-title h1 {
    margin: auto;
    text-align: left;
  }

  .infowindow-data {
    width: 500px;
  }

  .infowindow-data h1 {
    text-align: center;
    font-size: 1.2em;
  }

  .infowindow-data img {
    float: left;
    width: 25%;
    height: 50%;
    padding: 0 10px 0 0;
  }

  .infowindow-data p {
    font-family: arial;
    font-size: 1em;
  }

  .infowindow-link p {
    font-style: italic;
    margin-top: 1em;
    font-size: 1em;
  }

}





